.news-card {
  background: var(--background-alt);
  border-radius: var(--rounded);
  padding: 1rem;
  box-shadow: var(--shadow);
  margin-bottom: 1rem;
  transition: var(--transition);
}
.news-card:hover {
  transform: translateY(-3px);
}
.news-title {
  font-weight: var(--bold);
  font-size: 1.1rem;
}
.news-date {
  font-size: 0.9rem;
  color: var(--gray);
}

.news-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.news-title {
    font-weight: bold;
    font-size: 16px;
    text-align: left;
    
}

.news-date {
    font-size: 14px;
    color: gray;
}

.news-description {
    margin-top: 5px;
    font-size: 15px;
    text-align: left;
}

/* Responsive Design for Mobile */
@media (max-width: 600px) {
    .news-header {
        flex-direction: column;
        align-items: flex-start;
    }

    .news-date {
        margin-top: 5px;
    }
}